
// 配置错误告警页面

<template>
  <d2-container>
    <template slot="header">
      <el-select
        v-model="serviceName"
        placeholder="请选择服务"
        size="small"
        @change="changeService"
        filterable
        clearable
      >
        <el-option
          v-for="item in serviceOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>

      <el-select
        v-model="instanceValue"
        placeholder="请选择您的实例"
        size="small"
        @change="changeInstance"
        filterable
        clearable
        style="width: 200px"
      >
        <el-option
          v-for="(item, index) in instanceOptions"
          :key="index"
          :label="item.instanceId.split('#')[0]"
          :value="item.instanceId"
        >
        </el-option>
      </el-select>
    </template>

    <el-table
      v-loading="loading"
      :data="tableData"
      size="small"
      stripe
      highlight-current-row
      style="width: 100%; margin-top: 20px"
    >
      <el-table-column type="index" width="50" label="序号"></el-table-column>
      <el-table-column align="center" prop="id" label="ID"></el-table-column>
      <el-table-column
        align="center"
        prop="crt_time"
        label="时间"
      ></el-table-column>
      <el-table-column align="center" prop="config_miss_type" label="错误类型">
      </el-table-column>
      <el-table-column
        align="center"
        prop="miss_detail"
        label="告警内容"
      ></el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button @click="deleteClick(scope.row)" type="danger" size="small"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </d2-container>
</template>

<script>
import {
  fetchGetServiceList,
  fetchGetInstanceList,
  fetchGetConfigmiss,
  fetchDelConfigmiss,
} from "@/api/demo/index";
export default {
  name: "configmiss",
  data() {
    return {
      serviceName: "",
      serviceOptions: [],
      instanceOptions: [], //upf下面的实例
      instanceValue: "", //当前选中的实例
      timer: null,
      isFirst: true,
      timerInterval: 10000,
      loading: false,
      tableData: [],
      missTypeList: [
        { name: "Application ID 无配置", value: 1 },
        { name: "预定义规则无配置", value: 2 },
        { name: "网络实例无配置", value: 3 },
      ],
    };
  },
  mounted() {
    this.getService();
  },
  methods: {
    // 获取服务列表
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if (this.serviceOptions.length > 0) {
            this.serviceName = this.serviceOptions[0];
            this.getServiceInstance(this.serviceName);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 选择服务
    changeService(val) {
      if (!val) {
        return;
      }
      this.getServiceInstance(val);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.tableData = [];

      // 获取告警数据
      this.getBreakLink();
      //清除定时并重启
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
        this.setTimer();
      } else {
        this.setTimer();
      }
    },
    //获取服务下面的实例
    getServiceInstance(serviceName) {
      fetchGetInstanceList({ serviceName: serviceName })
        .then((res) => {
          this.instanceOptions = res.hosts;
          if (this.isFirst === true && this.instanceOptions.length > 0) {
            this.instanceValue = this.instanceOptions[0].instanceId;
            this.changeInstance(this.instanceValue);
          }
          this.isFirst = false;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    setTimer() {
      if (this.timer == null) {
        this.timer = setInterval(() => {
          console.log("开始定时...每过一-执行一次");
          this.getBreakLink();
        }, this.timerInterval);
      }
    },
    // 获取告警数据
    getBreakLink() {
      this.loading = true;
      fetchGetConfigmiss({
        request_type: "config miss",
        instance_id: this.instanceValue,
        sequence_no: 33451,
      })
        .then((response) => {
          this.loading = false;
          //刷新图表警告数据
          console.log(response);
          if ((response != null) & (response != [])) {
            this.tableData = response;
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 告警清除
    deleteClick(row) {
      this.$confirm("是否清除数据？", "确认信息", {
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消",
      })
        .then(() => {
          this.loading = true;
          fetchDelConfigmiss({
            request_type: "config miss",
            instance_id: this.instanceValue,
            sequence_no: 33452,
            data: {
              broken_type: row.config_miss_type,
            },
          })
            .then((response) => {
              this.loading = false;
              this.$message({
                type: "success",
                message: "操作成功",
              });
            })
            .catch((e) => {
              this.loading = false;
              console.log(e);
              this.$alert(e, "网络请求发生错误", {
                confirmButtonText: "确定",
                callback: (action) => {},
              });
            });
        })
        .catch((action) => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });
    },
  },
  destroyed: function () {
    // 每次离开当前界面时，清除定时器
    clearInterval(this.timer);
    this.timer = null;
  },
};
</script>
